.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #e80c7a;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  
  .main-image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
  }
  
  .main-image img {
    width: 105%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-3deg) translateY(2rem) translateX(-3rem);
  }

  .logo{
    padding-top: 20px;
    box-shadow: 5px 5px 12px rgb(0,0,0,0.75);
    width: 100px;
  }